.button-group {
    position: relative;
display: inline-flex;
vertical-align: middle;
}



.chart-btn {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: 0.3rem 0.5rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    color: #fff;
    background-color: #7896d6;
    border-color: #7896d6;
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    margin: 20px 1px;
    cursor: pointer
}

.percent-change-display {
    font-size: 1.3rem;
    text-align: center;
}

.market-rank {
    background-color: black;
    color: white;
    padding: 4px 6px;
}

.name-div {
    display: flex;
    margin: 5px 0;
    align-items: center;
    transform: translateX(-20px)
}

.coin-image {
    border-radius: 50%;
    width: 50px;
    margin: 10px;
}

.crypto-name {
    font-size: 1.6rem;
    font-size: bold;
}

.c-info-symbol {
    font-size: 1.3rem;
    color: rgb(87, 87, 87);
    margin: 0 5px;
}

.progress-bar {
    min-width: 400px;
}

.current-price {
    font-size: 1.55rem;
}

.price-change-24 {
    font-size: 1.2rem;
    margin-left: 5px;
}

.crypto-info-container {
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    width: 65%;
    padding: 2em 0;

}

.grid-box {
    display: flex;
    flex-direction: column;
    margin-top: 25px;
}

.grid-box > span {
    border-bottom: 1px solid rgba(211, 211, 211, 0.65);
    padding: 12px 0 ;
    font-size: 1.1rem;
}

.converter-div {
    display: flex;
    align-items: baseline;
    margin: 20px 0;
    justify-content: center;
}

.convert-input {
    display: flex;
    align-items: center;
}

.input {
    width: 80px;
    height: 32px;
    margin: 0 2px ;
}
.chart-container {
    width: 70%;
    padding-left: 2em;
}
.fa-exchange {
    margin: 0 10px;
}

.active-btn {
    background-color: #d7e5ffc7 !important;
}

.description-div {
    margin: 1.125rem 0;
}


.trending-div {
    display: flex;
    padding: 1em 0;
    flex-direction: column;
    width: 65%;
    margin: 0 auto;
    justify-content: space-evenly;
}

.trending-box {
    margin: 4px 0;
    padding: 1rem;
    font-size: 0.8rem;
}

.trending-flex-box {
    display: flex;
}
.trending-flex-box a {
    margin: 0 0.5rem;
    flex: 1 1;
}

.trending-image {
    background-image: url("../../public/images/cryptocurrency-golden-bitcoin-image.jpeg");
     background-size: cover;
     height: 96%;
       box-shadow:inset 0 0 0 2000px rgba(0, 0, 0, 0.472);
       border-radius: 4px;   
       -webkit-transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
       transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
       border-radius: 4px;
}
/* News styles  */

.main-news-wrapper {
    display: flex;
    padding: 1em 0;
    width: 65%;
    margin: 0 auto;
    justify-content: space-evenly;
}
.padding-container{
    margin-right: 3em;
}

.badge-container {
    margin: 1em 0;
}


.tag-badge {
    background-color: rgb(27, 100, 160);
    padding: 4px 8px;
    border-radius: 100px;
    display: inline-block;
    margin: 3px;
    color: white;
}
.news-div img {
    width: 250px;
}

.css-1h9z7r5-MuiButtonBase-root-MuiTab-root /* tab headers  */ {
    max-width: 50%;
}
.css-19kzrtu /* spinner div  */ {
    width: 250px;
    display: flex;
    justify-content: center;
    align-items: center;

}

@media screen and (max-width: 1600px) {
  
    .chart-container {
        width: 70%;
    }
    .main-news-wrapper {
        width: 70%;
    }
    .crypto-info-container {
        width: 70%;
    }
    .trending-div {
        width: 75%;
    }
}
@media screen and (max-width: 1500px) {
    .main-news-wrapper {
        width: 80%;
    }
    .crypto-info-container {
        width: 80%;
    }
    .trending-div {
        width: 85%;
    }
 
    

}

@media screen and (max-width: 1200px) {
    .crypto-info-container {
        width: 90%;
    }
    .chart-container {
        width: 70%;
    }
    .main-news-wrapper {
        width: 90%;
    }
    .trending-div {
        width: 90%;
    }
   
    
    .trending-flex-box a {
        margin: 0 0.2rem;
    }
   
}
@media screen and (max-width: 970px) {
    .crypto-info-container {
        flex-direction: column;
        padding:40px;
        width: 100%;
    }

    .chart-container {
        width: 100%;
        padding: 0;
    }

    .main-news-wrapper {
        flex-direction: column;
        width: 100%;
        padding: 40px;
    }

    .padding-container {
        margin: 0;
    }

    .trending-coins-div a {
        width: 100%;
        margin-top: 4px;
    }

    .trending-box {
        width: 100%;
    }

    .trending-box .trending-chart {
        display: none;
    }

    .trending-flex-box a:last-child {
        display: none;
    }

    .trending-coins-div {
        justify-content: center;
        flex-wrap: nowrap;
        flex-direction: column;
        width: 100%;
    }

    .trending-flex-box {
        justify-content: center;
        flex-wrap: nowrap;
        flex-direction: column;
        width: 100%;

    }
  


    .main-news-wrapper >  div > div{
        padding: 1em 0;
    }
    .description-div {
        font-size: 1em;
    }
    .news-div{
        margin: 0 auto;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        width: 350px;
    }
    #simple-tabpanel-0 {
    width: 100%;
    margin: 0 auto;
    }
   .news-div img {
       width: 350px;
   }
   .css-19kzrtu  /* spinner div  */ {
