    justify-content: center;
    align-items: center;
    width: 100%;
   }
   .css-1h9z7r5-MuiButtonBase-root-MuiTab-root /* tab headers  */ {
    min-width: 50% !important;
}
.css-1gsv261 {
    margin-bottom: 30px;
}

}



@media screen and (max-width: 600px) {
     .chart-container {
         padding: 0;
     }
     .badge-container {
         display: none;
     }
}

@media screen and (max-width: 500px) {
    .main-news-wrapper {
        padding: 20px;
    }
    .crypto-info-container {
        padding:0;
    }
    .crypto-info-container > div:first-child {
        padding:20px;
    }

}
@media screen and (max-width: 400px) {
