@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,700;1,100;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;900&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
}
body {
    background-color: #F4F7F9;

    overflow-x: hidden;
}

a {
    text-decoration: none;
    color: black;
}
.container-1 {
    display: flex;
    margin: 0 auto;
    width: 63%;
}
.main-section {
    background-color: rgb(255, 255, 255);
    width: 63%;
    border: 1px solid rgba(80, 80, 80, 0.514);
    margin: 0 auto;
    margin-bottom: 50px;
    transform: translateY(20px);
}


.sub-nav {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 0 auto;
    width: 65%;
    background-color: white;
    font-size: 0.8em;
}

.main-nav {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    align-items: center;
    padding: 6px;
    max-width: 100%;
    width: 65%;
    transition: left 0.2s ease 0s;
}
.nav-div {
    margin: 0 8px;
}
.nav-info {
    color: rgb(71, 122, 250);
}
.logo {
    display: flex;
    align-items: center;
}

.header-div {
    padding-top: 20px;
    display: flex;
    flex-direction: column;
}
.title{
    font-size: 2em;
    margin-top: 20px;
    margin-left: 380px;
}

#searchBar {
    height: 30px;
    width: 180px;
    border-radius: 10px;
    margin-top: 10px;
    border: 1px solid black;
    padding: 5px;
}
#searchBar:focus {
    outline: none;
}

.ordered-list .row-li {
    min-width: 100%;
    display: grid;
    grid-template-columns: 1fr 2fr 2.5fr 3fr 2.5fr 2.5fr;
    height: 85px;
    border-bottom: 1px solid rgba(131, 131, 131, 0.363);
    font-size: 0.875rem;
}

.ordered-list li {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.top-row {
    border-bottom:  1px solid rgba(114, 114, 114, 0.76);
    display: grid;
    grid-template-columns: 1fr 2fr 2.5fr 3fr 2.5fr 2.5fr;
    font-size: 0.925rem;
}
.top-row span {
    padding: 8px 30px;   
    font-size: 0.925rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.percent-div {
    display: flex;
    justify-content: space-around;
}




.row-li .rank {
    text-align: center;
}
.row-li span {
    text-align: center;
}
.row-li .name-symbol {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
}
.row-li .img-name {
    display: flex;
    margin-left: 10px;
    align-items: center;
}
.row-li .symbol {
    text-align: left;
    color: rgb(78, 78, 78);
    margin-top: 2px;
}
.row-li .name {
    text-align: left;
}
.market-graph {
    width: 100%;
    height: 75px;
}
.btn-group {
    display: flex;
    justify-content: center;
}
.highcharts-title {
    text-align: center;
}
.percent-change-red {
    color: #F1606D;
}
.percent-change-green {
    color: #33AD75;
}
.test {
    width: 35px;
    height: 35px;
}

footer {
    font-size: 1em;
    width: 100%;
    padding: 10px;
    text-align: center;
    margin-top: 40px;
    position: relative;
    background-color: white;

    bottom: 0;
}
footer a {
    color: black;
}



.load-btn {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    color: #fff;
    background-color: #7896d6;
    border-color: #7896d6;
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    margin: 20px auto;
}



.nav-container {
    width: 100%;
    border-bottom: 2px solid rgba(131, 131, 131, 0.363);
    background-color: white;
}


.nav-links {
    display: flex;
    justify-content: space-evenly;
    list-style: none;
    font-weight: 600;
    /* margin-right: 50px; */
}
.nav-links li {
    margin: 0 20px;

}
.menu-icon {
    display: none;
}

.menu-icon {
    display: none;
}
.close-icon {
    display: none;
}
  
.nav-links-modal {
    display: flex;
    flex-direction: column;
    background-color: white;
    height: 100vh;
    width: 100%;
    z-index: 1000;
    position: fixed;
    pointer-events: auto;
    right: -200vw;
    opacity: 100;
    transition: all 0.5s ease-in;
    overflow-y: scroll;

    bottom: 0px;
}


/* Search Input */

.link-search-container {
    display: flex;
    align-items: center;
}

.search-wrapper {
    width: 150px;
    height: 38px;
    border-radius: 5px;
    display:flex;
    flex-direction:row;
    align-items:center;
    border: 1px solid rgb(160, 160, 160)

  }

.search-input {
    all: unset;
    height: 100%;
    width: 100%;
    font-size: 0.80rem;
    padding: 0 10px;
  }

  #search-btn {
    all: unset;
    cursor: pointer;
    width: 44px;
    height: 44px;
  }
  
  .search-svg {
    fill: black;
    padding: 6px;
  }
  .flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

  /* ::placeholder {
    color: #fff;
    opacity: 0.7; 
  } */

@media screen and (max-width: 1600px) {
    .main-section {
        width: 70%;
    }
    .main-nav, .sub-nav {
        width: 73%;
    }
    .container-1  {
        width: 70%;
    }
   
}
@media screen and (max-width: 1500px) {
    .main-section {
        width: 80%;
    }
    .main-nav, .sub-nav {
        width: 83%;
    }
    .container-1  {
        width: 80%;
    }
   
}


@media screen and (max-width: 1350px) {
    .sub-nav .nav-div:nth-child(4) {
        display: none
    }
}

@media screen and (max-width: 1200px) {
    .main-section {
        width: 90%;
    }
    .main-nav {
        width: 93%;
    }
    .sub-nav {
        width: 90%;
    }
    .container-1 {
        width: 90%;
    }
    .sub-nav .nav-div:nth-child(4) {
        display: block
    }
    .top-row span {
        padding: 8px 20px;
    }
 
}
@media screen and (max-width: 900px) {
    .main-section {
        width: 100%;
        overflow-x: scroll;
    }
    .ordered-list .row-li {
        padding-right: 10px;
    }
}
@media screen and (max-width: 830px) {
    .info-box {
        display: none;
    }
    .container-1 {
        margin-left: 50px;
    }
    
    .top-row span {
        font-size:.79em;
        padding: 4px;
    }
    nav {
        width: 100vw;
    }

    .nav-div:nth-child(5) {
        display: none;
    }
    .nav-div:nth-child(4) {
        display: none;
    }
    .container-1 {
        width: 100%;
        margin: 0 auto;

        margin: 0;
    }   
    .header-div {
 
        width: 100%;;
    }
    #searchWrapper {
        display: flex;
        justify-content: center;
    }
    
    #searchBar {
        width: 90%;
        margin: 0 auto;
    }
  
    .ordered-list .row-li {
        min-width: 800px;
    }
    .top-row {
        min-width: 800px;
    }
    .menu-icon, .close-icon {
        margin-right: 15px;
    }
    .no-menu {
        display: none;
    } 
    .active-menu {
        display: block;
    } 
    .flex-menu {
        display: flex;
        margin: 16px 0;
        width: 200px;
        padding: 12px 5px;
    }
    .closed-nav-links {
       opacity: 0;
    }

    .nav-links {
        display: none;
    }

    .active-nav-links {
        transform: translateX(-200%);
    }
    .active-nav-links ul {
        list-style: none;
    }

    .active-nav-links li a {
        color: black;
    }

    .active-nav-links li {
        border-bottom:1px solid rgba(180, 180, 180, 0.76);
        padding: 14px 0;
        opacity: 100;
    }
    body.active-nav-links {
        overflow: hidden;
    }

   
    .link-search-container {
        display: none;
    }
}

@media screen and (max-width: 700px) {
    .coin-img {
        width: 25px;
        height: 25px;
    }
    .ordered-list .row-li {
       grid-template-columns: .5fr 2fr 1.5fr 3.2fr 2.5fr 2.5fr;
    }
    .top-row {
        grid-template-columns: .5fr 2fr 1.5fr 3.2fr 2.5fr 2.5fr;
        font-size: 1rem;
    }
    .ordered-list .row-li .rank {
        margin-left: 6px;
    }
    .top-row .t-rank {
        margin-left: 6px;
    }
}

@media screen and (max-width: 550px) {
    .nav-div:nth-child(2) {
        display: none;
    }
}

@media screen and (max-width: 430px) {
    .ordered-list .row-li {
        font-size: .8em;
    }
}

