
.info-container {
    display: flex;
    width: 100%;
}

.info-box {
    border: 1px solid rgb(230, 230, 230);
    margin: 15px 8px 0;
    padding: 12px 18px;
    border-radius: 15px;
    box-shadow: 0px 5px 8px 3px rgba(0, 0, 0, 0.06);
    background-color: white;
    width: 290px;
}

.info-name {
    margin: 6px 3px;
    font-size: 1.2rem;
}

.info-change {
    font-size: 1.2rem;
}

.info-desc {
    color: rgb(56, 56, 56);
    margin: 6px 0;

}

.info-coin-container > img {
    width: 35px;
    margin: 4px 8px;
}